html {
    scroll-behavior: smooth;
}

body {
    font-family: $fontPrimary;
    color: $IColor;
    background-color: $IColor;
    font-weight: 500;
    line-height: 1.17;
    margin: 0;
    /*
    font-weight: 400;
    line-height: 1.2;  -> movie title & description
    line-height: 1.33; -> paginations numbers
    font-size: 12px;
    font-size: 14px;
    font-size: 20px;
    font-size: 30px;
    */
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

ul {
    list-style: none;
    padding-left: 0;
    margin: 0;
}

a {
    text-decoration: none;
}

.container {
    width: 100%;
    padding: 20px 20px;
    margin: 0 auto;

    @media screen and (min-width: 320px) {
        width: 320px;
    }

    @media screen and (min-width: 768px) {
        width: 768px;
        padding: 60px 32px;
    }

    @media screen and (min-width: 1280px) {
        width: 1280px;
    }
}

.hidden {
    display: none;
}

.current {
    color: $lineColor;

    &::after {
        display: block;
        content: '';
        width: 34px;
        height: 3px;
        background: $lineColor;
        border-radius: 2px;
    }

    &__line {
        &::after {
            display: block;
            content: '';
            width: 69px;
            height: 3px;
            background: $lineColor;
            border-radius: 2px;
        }
    }
}